// Validador de referencias de archivos en el editor
import { FileManager } from './fileManager.js';

export class ReferenceValidator {
  constructor() {
    this.fileManager = new FileManager();
    this.validationCache = new Map();
    this.lastValidation = null;
  }

  // Validar todas las referencias en el proyecto
  async validateAllReferences(projectState) {
    try {
      console.log('🔍 Iniciando validación de referencias...');
      
      // Obtener todos los archivos disponibles
      const allFiles = await this.fileManager.getAllFiles();
      const fileIds = new Set(allFiles.map(f => f.id));
      const fileNames = new Map(allFiles.map(f => [f.name, f.id]));
      
      const brokenReferences = [];
      const warnings = [];
      
      // Validar referencias en archivos de escena
      if (projectState.sceneFiles) {
        for (const sceneFile of projectState.sceneFiles) {
          if (sceneFile.elements) {
            for (const element of sceneFile.elements) {
              const elementRefs = this.validateElementReferences(element, fileIds, fileNames);
              
              if (elementRefs.broken.length > 0) {
                brokenReferences.push({
                  sceneFileId: sceneFile.id,
                  sceneFileName: sceneFile.name,
                  elementId: element.id,
                  elementType: element.type,
                  broken: elementRefs.broken,
                  warnings: elementRefs.warnings
                });
              }
              
              if (elementRefs.warnings.length > 0) {
                warnings.push({
                  sceneFileId: sceneFile.id,
                  elementId: element.id,
                  warnings: elementRefs.warnings
                });
              }
            }
          }
        }
      }
      
      // Validar referencias en personajes
      if (projectState.characters) {
        for (const character of projectState.characters) {
          const characterRefs = this.validateCharacterReferences(character, fileIds, fileNames);
          
          if (characterRefs.broken.length > 0) {
            brokenReferences.push({
              type: 'character',
              characterId: character.id,
              characterName: character.name,
              broken: characterRefs.broken,
              warnings: characterRefs.warnings
            });
          }
        }
      }
      
      const validationResult = {
        isValid: brokenReferences.length === 0,
        brokenReferences: brokenReferences,
        warnings: warnings,
        totalFiles: allFiles.length,
        validatedAt: new Date().toISOString(),
        summary: {
          totalBroken: brokenReferences.length,
          totalWarnings: warnings.length,
          brokenByType: this.categorizeBrokenReferences(brokenReferences)
        }
      };
      
      // Cachear resultado
      this.validationCache.set('last', validationResult);
      this.lastValidation = validationResult;
      
      console.log('✅ Validación completada:', validationResult.summary);
      return validationResult;
      
    } catch (error) {
      console.error('❌ Error durante validación de referencias:', error);
      return {
        isValid: false,
        error: error.message,
        brokenReferences: [],
        warnings: [],
        validatedAt: new Date().toISOString()
      };
    }
  }

  // Validar referencias de un elemento específico
  validateElementReferences(element, fileIds, fileNames) {
    const broken = [];
    const warnings = [];
    
    // Validar fondo
    if (element.background) {
      if (!fileIds.has(element.background)) {
        // Buscar por nombre como fallback
        const foundByName = this.findFileByName(element.background, fileNames);
        if (foundByName) {
          warnings.push({
            type: 'background',
            reference: element.background,
            suggestion: `Usar ID: ${foundByName} en lugar de nombre`,
            fix: foundByName
          });
        } else {
          broken.push({
            type: 'background',
            reference: element.background,
            message: 'Archivo de fondo no encontrado'
          });
        }
      }
    }
    
    // Validar sprite
    if (element.sprite) {
      if (!fileIds.has(element.sprite)) {
        const foundByName = this.findFileByName(element.sprite, fileNames);
        if (foundByName) {
          warnings.push({
            type: 'sprite',
            reference: element.sprite,
            suggestion: `Usar ID: ${foundByName} en lugar de nombre`,
            fix: foundByName
          });
        } else {
          broken.push({
            type: 'sprite',
            reference: element.sprite,
            message: 'Archivo de sprite no encontrado'
          });
        }
      }
    }
    
    // Validar música
    if (element.music) {
      if (!fileIds.has(element.music)) {
        const foundByName = this.findFileByName(element.music, fileNames);
        if (foundByName) {
          warnings.push({
            type: 'music',
            reference: element.music,
            suggestion: `Usar ID: ${foundByName} en lugar de nombre`,
            fix: foundByName
          });
        } else {
          broken.push({
            type: 'music',
            reference: element.music,
            message: 'Archivo de música no encontrado'
          });
        }
      }
    }
    
    // Validar sonido
    if (element.sound) {
      if (!fileIds.has(element.sound)) {
        const foundByName = this.findFileByName(element.sound, fileNames);
        if (foundByName) {
          warnings.push({
            type: 'sound',
            reference: element.sound,
            suggestion: `Usar ID: ${foundByName} en lugar de nombre`,
            fix: foundByName
          });
        } else {
          broken.push({
            type: 'sound',
            reference: element.sound,
            message: 'Archivo de sonido no encontrado'
          });
        }
      }
    }
    
    // Validar video
    if (element.video) {
      if (!fileIds.has(element.video)) {
        const foundByName = this.findFileByName(element.video, fileNames);
        if (foundByName) {
          warnings.push({
            type: 'video',
            reference: element.video,
            suggestion: `Usar ID: ${foundByName} en lugar de nombre`,
            fix: foundByName
          });
        } else {
          broken.push({
            type: 'video',
            reference: element.video,
            message: 'Archivo de video no encontrado'
          });
        }
      }
    }
    
    return { broken, warnings };
  }

  // Validar referencias de un personaje
  validateCharacterReferences(character, fileIds, fileNames) {
    const broken = [];
    const warnings = [];
    
    // Validar sprites del personaje
    if (character.sprites && Array.isArray(character.sprites)) {
      for (const sprite of character.sprites) {
        if (sprite.fileId && !fileIds.has(sprite.fileId)) {
          const foundByName = this.findFileByName(sprite.name, fileNames);
          if (foundByName) {
            warnings.push({
              type: 'character_sprite',
              reference: sprite.fileId,
              spriteName: sprite.name,
              suggestion: `Usar ID: ${foundByName} en lugar de nombre`,
              fix: foundByName
            });
          } else {
            broken.push({
              type: 'character_sprite',
              reference: sprite.fileId,
              spriteName: sprite.name,
              message: 'Sprite de personaje no encontrado'
            });
          }
        }
      }
    }
    
    return { broken, warnings };
  }

  // Buscar archivo por nombre
  findFileByName(fileName, fileNames) {
    // Buscar coincidencia exacta
    if (fileNames.has(fileName)) {
      return fileNames.get(fileName);
    }
    
    // Buscar coincidencia parcial (sin extensión)
    const nameWithoutExt = fileName.replace(/\.[^/.]+$/, '');
    for (const [name, id] of fileNames) {
      if (name.replace(/\.[^/.]+$/, '') === nameWithoutExt) {
        return id;
      }
    }
    
    return null;
  }

  // Categorizar referencias rotas por tipo
  categorizeBrokenReferences(brokenReferences) {
    const categories = {
      background: 0,
      sprite: 0,
      music: 0,
      sound: 0,
      video: 0,
      character_sprite: 0,
      element: 0,
      character: 0
    };
    
    brokenReferences.forEach(ref => {
      if (ref.type === 'character') {
        categories.character++;
      } else if (ref.elementType) {
        categories.element++;
        ref.broken.forEach(broken => {
          if (categories.hasOwnProperty(broken.type)) {
            categories[broken.type]++;
          }
        });
      }
    });
    
    return categories;
  }

  // Limpiar referencias rotas automáticamente
  async cleanupBrokenReferences(projectState, options = {}) {
    try {
      console.log('🧹 Iniciando limpieza automática de referencias rotas...');
      
      const {
        removeBroken = true,
        fixWarnings = false,
        dryRun = false
      } = options;
      
      const validation = await this.validateAllReferences(projectState);
      
      if (validation.isValid) {
        console.log('✅ No se encontraron referencias rotas para limpiar');
        return { cleaned: 0, fixed: 0, removed: 0 };
      }
      
      let cleaned = 0;
      let fixed = 0;
      let removed = 0;
      
      // Crear copia del estado para modificar
      const updatedState = JSON.parse(JSON.stringify(projectState));
      
      // Limpiar referencias en elementos
      if (updatedState.sceneFiles) {
        for (const sceneFile of updatedState.sceneFiles) {
          if (sceneFile.elements) {
            for (const element of sceneFile.elements) {
              const elementRefs = validation.brokenReferences.find(
                ref => ref.elementId === element.id
              );
              
              if (elementRefs) {
                // Limpiar referencias rotas
                if (removeBroken) {
                  elementRefs.broken.forEach(broken => {
                    if (broken.type === 'background') {
                      element.background = null;
                      removed++;
                    } else if (broken.type === 'sprite') {
                      element.sprite = null;
                      element.showSprite = false;
                      removed++;
                    } else if (broken.type === 'music') {
                      element.music = null;
                      removed++;
                    } else if (broken.type === 'sound') {
                      element.sound = null;
                      removed++;
                    } else if (broken.type === 'video') {
                      element.video = null;
                      removed++;
                    }
                  });
                }
                
                // Corregir advertencias
                if (fixWarnings) {
                  elementRefs.warnings.forEach(warning => {
                    if (warning.fix) {
                      if (warning.type === 'background') {
                        element.background = warning.fix;
                        fixed++;
                      } else if (warning.type === 'sprite') {
                        element.sprite = warning.fix;
                        fixed++;
                      } else if (warning.type === 'music') {
                        element.music = warning.fix;
                        fixed++;
                      } else if (warning.type === 'sound') {
                        element.sound = warning.fix;
                        fixed++;
                      } else if (warning.type === 'video') {
                        element.video = warning.fix;
                        fixed++;
                      }
                    }
                  });
                }
                
                cleaned++;
              }
            }
          }
        }
      }
      
      // Limpiar referencias en personajes
      if (updatedState.characters) {
        for (const character of updatedState.characters) {
          const characterRefs = validation.brokenReferences.find(
            ref => ref.characterId === character.id
          );
          
          if (characterRefs) {
            if (removeBroken && character.sprites) {
              character.sprites = character.sprites.filter(sprite => {
                const isBroken = characterRefs.broken.some(
                  broken => broken.type === 'character_sprite' && broken.reference === sprite.fileId
                );
                if (isBroken) {
                  removed++;
                  return false;
                }
                return true;
              });
            }
            
            if (fixWarnings && character.sprites) {
              character.sprites.forEach(sprite => {
                const warning = characterRefs.warnings.find(
                  w => w.type === 'character_sprite' && w.reference === sprite.fileId
                );
                if (warning && warning.fix) {
                  sprite.fileId = warning.fix;
                  fixed++;
                }
              });
            }
            
            cleaned++;
          }
        }
      }
      
      const result = {
        cleaned,
        fixed,
        removed,
        dryRun,
        updatedState: dryRun ? null : updatedState
      };
      
      if (!dryRun) {
        console.log(`✅ Limpieza completada: ${cleaned} elementos, ${fixed} corregidos, ${removed} eliminados`);
      } else {
        console.log(`🔍 Simulación completada: ${cleaned} elementos, ${fixed} corregidos, ${removed} eliminados`);
      }
      
      return result;
      
    } catch (error) {
      console.error('❌ Error durante limpieza de referencias:', error);
      return { error: error.message, cleaned: 0, fixed: 0, removed: 0 };
    }
  }

  // Obtener estadísticas de validación
  getValidationStats() {
    if (!this.lastValidation) {
      return null;
    }
    
    return {
      lastValidation: this.lastValidation.validatedAt,
      totalBroken: this.lastValidation.summary.totalBroken,
      totalWarnings: this.lastValidation.summary.totalWarnings,
      brokenByType: this.lastValidation.summary.brokenByType,
      isValid: this.lastValidation.isValid
    };
  }

  // Limpiar caché de validación
  clearCache() {
    this.validationCache.clear();
    this.lastValidation = null;
    console.log('🗑️ Caché de validación limpiada');
  }
}
